@import "../inc/config";
.uk-form-label {
      color: $color3;
      font: 500 11px/11px "Roboto";
      margin: 10px 5px;
      letter-spacing: 1px;
      text-transform: uppercase;


}
  .uk-form-controls {
    margin-top: 5px;
  }
.uk-input, .uk-select, .uk-textarea {
  width: 100%;
  background: transparent;
  border: 1px solid #9ca2a5;
  color: #fff;
  border-radius: 10px;
    font: 400 12px/16px $font;
    border: 1px solid $color0;
  background: $color1;

  &:active,
  &:focus {
    color:#fff;
    background: transparent;
    transition: all 0.5s;
    border-color: $color2;
  }
}
.uk-textarea {
  border: 1px solid $color0;
background: $color1;
}
input[type="time"] {
  padding: 20px 10px;
    font: 400 15px/16px $font !important;
}

.uk-button {
  background: $color1;
  letter-spacing: 1px;
  text-transform: uppercase;
  font: 400 11px/16px $font;
  padding: 15px 20px;
  border-radius: 10px;
  border: 0;
  color: #fff;

  &:focus,
  &:hover {
    transition: all 0.5s;
    background: $color2;
  }
  &.uk-button-primary {
    background: $color2;
    color:$color0;
  }
}
.uk-text-right {
  button:first-child{
    margin-right: 10px;
  }
}
.uk-modal-dialog {
  background: $color0;
}

.ql-snow .ql-stroke {
    stroke: $color3 !important;
}
.ql-toolbar.ql-snow {
  border: 0px !important;
  background: $color1;
}
.ql-snow .ql-picker-options {
  background: $color0 !important;
}
.ql-container.ql-snow, .ql-toolbar.ql-snow .ql-picker-label, .ql-snow .ql-picker-options {
    border: 0px !important;
}
.ql-snow.ql-toolbar .ql-picker-label:hover {
  color:$color2 !important;
}
.ql-editor {
    background: $color1;
}
.ql-snow .ql-picker {
  color:$color3 !important;
}
.bar {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;

  h1 {
    color: #9ca2a5;
    letter-spacing: 2px;
    text-transform: uppercase;
    font: 500 12px/14px $font;
  }
}



  .logo {
    color: #fff;
    letter-spacing: 1px;
    text-transform: uppercase;
    font: 600 14px/14px $font;

    span {
      margin-left: 2px;
      color: $color2;
      font-weight: 800;
    }
  }
